.app-wrapper {
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 80px 1fr;
    grid-auto-columns: 2fr 10fr;
    grid-gap: 10px;
    grid-template-areas:
    "h h"
    "n c"
;
}

.app-wrapper-content {
    grid-area: c;
    background-color: blueviolet;
}

